.animeTest {
  background-color: aqua;
  width: 50px;
  height: 50px;
}
.App-header {
  min-height: 180vh;
  transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
  position: relative;
}
.SecondPart {
  transition: 1s;
  height: auto;
  div{
    z-index: 50
  }
}
.projects {
  display: flex;
  width: 100%;
  flex-direction: row;
  margin-bottom: 200px;
  .svg {
    width: 60vw;
    padding-right: 10% !important;
    padding-left: 5%;
    align-items: center;
    display: flex
  }
  .text {
    max-width: 710px;
    margin-right: 10vw;
    position: relative;
    align-items: center;
    display: flex;
    p {
      font-size: 1.5vw
    }
    h1 {
      font-size: 2.8vw
    }
    .svgButton {
      position: absolute;
      left: 15%;
    }
  }
}
.skills-white div{
  background-color: rgb(238, 238, 238) !important;
  box-shadow: 5px 5px 30px 0px rgb(238, 238, 238);
}
.skills {
  display: grid;
  grid-auto-flow: row;
  grid-template-columns: auto auto auto;
  grid-row-gap: 5vh;
  align-items: center;
  justify-items: center;
  div {
    width: 20vw;
    height: 20vw;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 20px;
    align-items: center;
    justify-content: center;
    display: flex;
  }
  svg {
    position: absolute;
    width: 20vw;
    transition: .5s cubic-bezier(0.165, 0.84, 0.44, 1)
  }
  h4 {
    position: relative;
    text-align: center;
    align-self: flex-start;
    margin-top: 15px;
    width: 100%;
    justify-self: center;
    color: white;
    opacity: 0;
    font-size: 2vw;
    transition: .5s
  }
}
.skillsTitle {
  text-align: center;
  font-size: 64px
}
.skills :hover {
  svg {
    width: 15vw;
  }
  h4 {
    opacity: 1;
  }
}
.rectangels svg {
  position: absolute;
  z-index: 20;
}
.heroBottomWave {
  position: absolute;
  bottom: 0;
  right: 0 !important;
  width: 100%;
}
.waveParallaxLayer {
  position: absolute;
  bottom: 0;
}
.slide1 {
  width: 100%;
  z-index: 1000;
  position: absolute
}
.WaveContainer {
  position: relative;
  width: 100%
}
.heroText {
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 50;
  padding-top: 40vh;
  transform: translateZ(1000px);
  transform-style: preserve-3d;
  align-items: center;
  .heroTitle {
    text-align: center;
    width: 70%;
    margin: 0;
    opacity: 0;
    color: #2c97aa;
    animation: HeroAnimation 3s cubic-bezier(0.2, 0.8, 0.2, 1) .1s 1 normal forwards;
  }
  .heroTitleText {
    text-align: center;
    font-size: 24px;
    opacity: 0;
    animation: HeroAnimation 3s cubic-bezier(0.2, 0.8, 0.2, 1) .2s 1 normal forwards;
  }
}
.rectangels {
  position: relative;
  width: 100%;
  height: 180vh;
  overflow: hidden;
  .svgTopRight {
    top: -100px;
    right: -100px;
  }
  .svgRight {
    right: -80px;
    top: 50%
  }
  .svgSmallRight {
    right: 25%;
    top: 40%
  }
  .svgCenter {
    top: 30%;
    right: calc(50% - 131px)
  }
  .svgSmallLeft {
    left: 25%;
    top: 24%
  }
  .svgTop {
    top: -120px;
    left: 5%
  }
  .svgLeft {
    left: -109px;
    top: 40%
  }
  .svgBottomCenter {
    top: 60%;
    right: 35%
  }
}
.footer-white{
  .main {
    background-color: rgb(18%, 15%, 15%) !important
  }
  svg {
    fill: rgb(18%, 15%, 15%) !important
  }
}
.footer {
  width: 100%;
  height: auto;
  margin-top: 150px;
  .main {
    width: 100%;
    height: 150px;
    background-color: rgba(0, 0, 0, 0.28);
  }
  .svg {
    width: 100%;
    height: auto;
    position: relative;
    .img {
      width: 100%;
      align-items: center;
      justify-content: center;
      display: flex;
      svg {
        fill: rgba(0, 0, 0, 0.28)
      }
    }
    .txt {
      text-align: center;
      width: 100%;
      align-items: center;
      justify-content: center;
      display: flex;
      position: absolute;
      top: 0;
      height: 100%;
      color: rgba(255, 255, 255, 0.7);
      font-size: 1vw;
    }
  }
}
@media (max-width: 1070px) {
  .svgSmallLeft {
    left: 15% !important;
    top: 40%
  }
}
@media (max-width: 950px) {
  .svgSmallRight {
    left: 20% !important;
    top: 80% !important
  }
}
@media (max-width: 900px) {
  .skills :hover {
    svg {
      width: 15vw;
    }
    h4 {
      opacity: 0;
    }
  }
}
@media (max-width: 800px) {
  .rectangels svg {
    display: none
  }
}
@media (max-height: 450px) {
  .rectangels svg {
    display: none
  }
}
@media (max-height: 610px) {
  .svgLeft {
    display: none
  }
  .svgRight {
    display: none
  }
  .svgBottomCenter{
    display: none
  }
  .heroBottomWave {
    display: none
  }
  .slide1 {
    padding-top: 80px;
    margin-top: -20vh
  }
}
@media (max-height: 668px) {
  .svgLeft {
    top: 30% !important;
  }
  .svgRight {
    top: 45% !important;
  }
}
@keyframes HeroAnimation {
  0% {
    opacity: 0;
    transform: translateY(20px)
  }
  100% {
    opacity: 1;
    transform: translateY(0px)
  }
}