.header {
  padding-top: 50px;
  width: 100%;
  position: fixed;
  z-index: 102;
  display: block;
  transform: translateZ(1000px);
  transform-style: preserve-3d;
}

.HeaderGroup {
  padding-top: 15px;
  padding-bottom: 15px;
  max-width: 800px;
  margin: auto;
  text-align: center;
  display: grid;
  grid-template-columns: auto;
  grid-auto-flow: column;
  align-items: center;
  justify-items: center;
  border-radius: 20px;
}

.svgToggleContainer svg {
  background-color: rgb(86, 233, 243);
  border-radius: 100px;
  padding: 8px 8px 8px 8px;
  transition: 1s cubic-bezier(0.2, 0.8, 0.2, 1);
}

.svgToggleContainer:hover {
  svg {
  background-color: white !important;
  }
}

button {
  color: white;
  font-weight: 600;
  font-size: 20px;
  border-radius: 10px;
  background-color: transparent;
  border-color: transparent;
  outline: 0ch;
  padding: 5px 20px 5px 20px;
  transition: 1s cubic-bezier(0.2, 0.8, 0.2, 1);
}
button:hover {
  color: black;
  background-color: white !important;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 10px 20px;
  outline: 0ch;
  border-color: transparent;
  cursor: pointer;
  transform: translateY(-3px);
}
.mobileHeader {
  display: none;
  z-index: 1000;
}
#mobileMenu {
  display: none;
}
.bigLogo {
  background-image: url('./logo-dark.svg');
  height: 48px;
  width: 50px;
  background-size: cover
}

@media (max-width: 767px) {
  .header {
    display: none;
  }
  .mobileHeader{
    width: 100%;
    height: 50px;
    background-color: rgba(0, 0, 0,0.8);
    position: fixed;
    display: grid;
    grid-template-columns: 50px auto 50px;
    justify-items: center;
    align-items: center
  }
  #mobileMenu {
    display: block;
    width: 100%;
    position: fixed;
    top: 50px;
    background-color: rgba(0, 0, 0,0.8);
    z-index: 100;
    transition: 3s cubic-bezier(0.1, 0.8, 0.2, 1);
    max-height: 0px;
    overflow: hidden;
  }
  #mobileMenu div {
    padding: 15px;
    width: 100%;
    border-top: 1px solid rgba(202, 202, 202, 0.733);
    outline: none
  }
  #mobileMenu a {
    text-decoration: none;
    color: white;
    font-size: 20px;
  }
  #svg1 {
    height: 5px;
    width: 30px;
    transition: 1s cubic-bezier(0.1, 0.8, 0.2, 1);
    margin-bottom: 8px;
    position: relative;
  }
  #svg2 {
    height: 5px;
    width: 30px;
    transition: 1s cubic-bezier(0.1, 0.8, 0.2, 1);
    position: relative;
  }
  #svgContainer {
    justify-content: center;
    align-items: center;
    display: flex
  }
  #completeSvg {
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column
  }
  .brand {
    color: white;
    display: flex;
    height: 40px;
    width: 40px;
  }
  .car {
    background-image: url('./car.svg');
    height: 40px;
    width: 40px;
    background-size: cover
  }
}
