h3 {
  color: white;
  position: absolute;
  right: 30%;
  top: 5%;
  font-size: 2rem;
  font-weight: 200;
  letter-spacing: 3px
}
.Container {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.close {
  position: absolute;
  right: 0px
}
.Project-absolute {
  position: absolute;
  height: 100%;
  width: 55%;
  background-color: #00BCD4;
  bottom: -100%;
}
.placeholder-absolute {
  position: absolute;
  height: 100%;
  width: 10%;
  right: 35%;
  background-color: #1C1D21;
  bottom: -100%;
  box-shadow: -3px 0 15px rgba(0, 0, 0, 0.5);
}
.ProjectMenu-absolute {
  height: 100%;
  width: 35%;
  position: absolute;
  right: 0px;
  background-color: #25262A;
  bottom: -80%;
  opacity: 0;
  box-shadow: -3px 0 15px rgba(0, 0, 0, 0.5);
}
.buttonFix button:hover {
  background-color: transparent !important;
  transform: translateY(0px) !important;
  box-shadow: none !important
}
.Git {
  position: absolute;
  left: 5px;
  top: -50px;
  opacity: 0
}
.more {
  position: absolute;
  top: calc(100% + 100px);
  left: 130px;
  background-color: rgba(0, 0, 0, 0.2) !important;
  border-radius: 0px;
  padding: 5px 20px 100px 20px;
  color: white;
  transition: 1s
}
.id {
  font-size: 3rem;
  color: rgba(37, 37, 37, 0.459);
  position: absolute;
  right: -80px;
  margin-top: 30px;
}
.title {
  color: white;
  font-size: 3.5rem;
  position: absolute;
  margin: 0;
  left: 50px;
  top: 40vh;
  max-height: 0;
  overflow: hidden
}
.about {
  position: absolute;
  color: white;
  font-size: medium;
  top: 60vh;
  left: 130px;
  max-width: 60%;
  overflow: hidden
}
.company {
  position: absolute;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.45);
  font-size: 1.8rem;
  font-weight: bold;
  left: 50px;
  top: calc(40vh - 80px);
  max-width: 0px;
  overflow: hidden;
}
.year {
  position: absolute;
  width: 1em;
  text-align: center;
  word-break: break-all;
  text-orientation: upright;
  color: white;
  font-weight: bold;
  right: 50px;
  margin: 0;
  top: calc(45vh + 80px);
  overflow: hidden;
  max-height: 0
}
.line {
  height: 5px;
  width: 80px;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  right: -80px;
  top: calc(45vh + 80px);
  border-radius: 20px
}
.list {
  color: white;
  list-style-type: none;
  position: absolute;
  top: 15%;
  width: 100%;
  padding: 0;
}
.list li {
  padding-left: 15%;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center
}
.list li:hover {
  background-color: rgba(255, 255, 255, 0.222)
}
.list-component div {
  height: 5px;
  width: 0px;
  transition: .5s;
  background-color: #00BCD4;
  margin-right: 10px;
  border-radius: 20px
}
.list-component:hover div {
  width: 20px
}